*{
  overflow-x: hidden;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bxs-star{
  justify-self: center;
  -webkit-text-stroke: 1px black;
}

/*#region CATEGORYCARD */
.category-card{
  border: 3px solid black;
  padding-inline: 3rem;
  padding-block: 0.3rem;
  margin: 0.6rem;
  text-align: center;
  width: fit-content;
  height: fit-content;
  border-radius: 6px;
  cursor: pointer;
}

.category-card-active{
  border: 3px solid black;
  padding-inline: 3rem;
  padding-block: 0.3rem;
  margin: 0.6rem;
  text-align: center;
  width: fit-content;
  height: fit-content;
  border-radius: 6px;
  cursor: pointer;
  transform: translateY(-2px);
  box-shadow: rgba(0, 0, 0, 0.5) 0 8px 15px;
}

.category-card:hover{
  transform: translateY(-2px);
  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
}

.category-card-title{
  text-transform: capitalize;
  width: fit-content;
}
/*#endregion */

/*#region SIDEBAR */
.side-bar{
  margin: 1.2rem;
  border: 2.1px solid black;
  height: fit-content;
  width: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 0.9rem;
  position: relative;
}

.bx-reset{
  position: absolute;
  top: 0.3rem;
  right: 0.3rem;
  font-size: 21px;
  cursor: pointer;
}

.bx-reset:hover{
  text-shadow: 0.1px 0.6px 1px gray;
}

.sb-pricerange{
  width: 18vw;
  align-self: center;
  border: 1.2px solid black;
  margin-block: 0.9rem;
  border-radius: 6px;
}

.sb-pricerange-title{
  margin-inline: 1rem;
  font-weight: 500;
}

.sb-pricerange-inputs{
  display: flex;
  justify-content: space-between;
  margin-inline: 1rem;
}

.sb-pricerange-inputs input{
  min-width: 3rem;
  max-width: 5.7rem;
  padding-inline: 0.3rem;
  padding-block: 0.33rem;
  border-radius: 6px;
  border: 1px solid rgba(128, 128, 128, 0.6);
  margin-bottom: 1rem;
}

.sb-pricerange-buttons{
  display: flex;
  width: auto;
  justify-content: center;
}

.sb-pricerange-button{
  margin-inline: 1rem;
  margin-bottom: 1rem;
  padding-inline: 1.8rem;
  padding-block: 0.3rem;
  background-color: #fff;
  border: 1px solid black;
  font-weight: 600;
}

.sb-pricerange-button:hover{
  box-shadow: 0.6px 0.6px 1.5px black;
}

.sb-color{
  width: 18vw;
  align-self: center;
  border: 1.2px solid black;
  margin-block: 0.9rem;
  border-radius: 6px;
  padding-bottom: 0.69rem;
}

.sb-color-title{
  margin-inline: 1rem;
  font-weight: 500;
}

.sb-color input{
  width: 7.5rem;
  padding-inline: 0.3rem;
  padding-block: 0.33rem;
  margin-bottom: 1rem;
  margin-inline: 1rem;
  border-radius: 6px;
  border: 1px solid rgba(128, 128, 128, 0.6);
}

.sb-color-selectors{
  font-weight: 500;
}

.sb-color-selectors label{
  font-weight: 500;
  display: flex;
  align-content: center;
  align-items: center;
  align-self: center;
}

.sb-color-selectors input{
  width: fit-content;
  margin-inline: 1rem;
  margin-block: 0.45rem;
  border: 1px solid rgba(128, 128, 128, 0.6);
}

.sb-rating{
  width: 18vw;
  align-self: center;
  border: 1.2px solid black;
  margin-block: 0.9rem;
  border-radius: 6px;
}

.sb-rating-title{
  margin-inline: 1rem;
  font-weight: 500;
}

.sb-rating-stars{
  margin-inline: 1rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 1rem;
}

.sb-rating-boxs{
  display: flex;
  width: fit-content;
  align-self: start;
  margin-bottom: 0.12rem;
  gap: 1rem;
  cursor: pointer;
  padding: 3px;
  margin-left: 0.15rem;
}

.sb-rating-label{
  display: flex;
  margin-block: 0.33rem;
}
/*#endregion */

/*#region PRODUCTCARD */
.product-card{
  border: 1px solid black;
  min-width: 18vw;
  max-width: 21vw;
  height: fit-content;
  border-radius: 9px;
  margin: 1.2rem;
  box-shadow: 1px 0.5px 6px black;
}

.pc-content{
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.pc-img-box{
  display: grid;
  width: 100%;
  height: 100%;
  place-items: center;
}

.pc-img{
  margin-top: 1rem;
  width: 81%;
  border: 1px solid black;
  border-radius: 6px;
}

.pc-title-box{
  display: grid;
  place-items: center;
}

.pc-title{
  font-weight: 500;
}

.pc-rating-box{
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  width: 60%;
  place-content: center;
  place-self: center;
}

.pc-price-box{
  display: grid;
  place-items: center;
}

.pc-price{
  font-weight: 400;
}
/*#endregion */

/*#region INDEXPAGE*/
.index-page{
  width: 100vw;
  height: 100vh;
}

.index-categories{
  display: flex;
  justify-content: space-evenly;
  padding-bottom: 0.45rem;
}

.index-content{
  display: flex;
}

.index-sidebarcardcontent{
  width: fit-content;
  height: fit-content;
}

.index-productcardcontent{
  display: grid;
  width: 75vw;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(6, 1fr);
}
/*#endregion*/